.nelio-ab-testing-confetti-cone {

	&__conf0 {
		fill: #fc6394;
	}

	&__conf1 {
		fill: #ef3c8a;
	}

	&__conf2 {
		fill: #5adaea;
	}

	&__conf3 {
		fill: #974cbe;
	}

	&__conf4 {
		fill: #3cbecd;
	}

	&__conf5 {
		fill: #813bbe;
	}

	&__conf6 {
		fill: #f9b732;
	}

	&__conf7 {
		display: none;
		fill: none;
		stroke: #000;
		stroke-miterlimit: 10;
	}

	&__conf8 {
		fill: none;
		stroke: #f9b732;
		stroke-width: 9;
		stroke-linecap: round;
		stroke-miterlimit: 10;
	}

	&__group {
		transform-origin: 200px 50px;
		animation: nelio-ab-testing-confetti-cone-frames__confetti-cone1 1.2s
			ease infinite;
	}

	&__yellow-strip {
		fill: none;
		stroke: #f9b732;
		stroke-width: 9;
		stroke-linecap: round;
		stroke-miterlimit: 10;
		animation: nelio-ab-testing-confetti-cone-frames__confdash 1.2s ease
			infinite;
	}

	&__a2 {
		transform-origin: 310.9px 241px;
		animation: nelio-ab-testing-confetti-cone-frames__confa 1.2s ease-out
			infinite;
	}

	&__a1 {
		transform-origin: 276px 246px;
		animation: nelio-ab-testing-confetti-cone-frames__confa 1.2s ease-out
			infinite;
	}

	&__b1 {
		transform-origin: 195.2px 232.6px;
		animation: nelio-ab-testing-confetti-cone-frames__confb 1.2s ease-out
			infinite;
	}

	&__b2 {
		transform-origin: 230.8px 219.8px;
		animation: nelio-ab-testing-confetti-cone-frames__confb 1.2s ease-out
			infinite;
	}

	&__b3 {
		transform-origin: 222.8px 190.6px;
		animation: nelio-ab-testing-confetti-cone-frames__confb 1.2s ease-out
			infinite;
	}

	&__b4 {
		transform-origin: 262px 188.5px;
		animation: nelio-ab-testing-confetti-cone-frames__confb 1.2s ease-out
			infinite;
	}

	&__b5 {
		transform-origin: 282.3px 170.6px;
		animation: nelio-ab-testing-confetti-cone-frames__confb 1.2s ease-out
			infinite;
	}

	&__c1 {
		transform-origin: 174.8px 183.4px;
		animation: nelio-ab-testing-confetti-cone-frames__confc 1.2s ease-out
			infinite;
	}

	&__c2 {
		transform-origin: 178.9px 156.2px;
		animation: nelio-ab-testing-confetti-cone-frames__confc 1.2s ease-out
			infinite;
	}

	&__c3 {
		transform-origin: 206.7px 140px;
		animation: nelio-ab-testing-confetti-cone-frames__confc 1.2s ease-out
			infinite;
	}

	&__c4 {
		transform-origin: 213.5px 120.2px;
		animation: nelio-ab-testing-confetti-cone-frames__confc 1.2s ease-out
			infinite;
	}

	&__d1 {
		transform-origin: 127px 176px;
		animation: nelio-ab-testing-confetti-cone-frames__confd 1.2s ease-out
			infinite;
	}

	&__d2 {
		transform-origin: 133px 118px;
		animation: nelio-ab-testing-confetti-cone-frames__confd 1.2s ease-out
			infinite;
	}

	&__d3 {
		transform-origin: 152px 100px;
		animation: nelio-ab-testing-confetti-cone-frames__confd 1.2s ease-out
			infinite;
	}
}

@keyframes nelio-ab-testing-confetti-cone-frames__confetti-cone1 {

	0% {
		transform: translate(40px, 95px) rotate(45deg) scale(1, 1);
	}

	15% {
		transform: translate(10px, 145px) rotate(45deg) scale(1.1, 0.85);
	}

	100% {
		transform: translate(40px, 105px) rotate(45deg) scale(1, 1);
	}
}

@keyframes nelio-ab-testing-confetti-cone-frames__confdash {

	0% {
		stroke-dasharray: 1000;
		stroke-dashoffset: 500;
		transform: translate(-30px, 30px);
		opacity: 0;
	}

	2% {
		stroke-dasharray: 1000;
		stroke-dashoffset: 500;
		transform: translate(-30px, 30px);
		opacity: 0;
	}

	35% {
		stroke-dasharray: 1000;
		stroke-dashoffset: 900;
		transform: translate(-2px, 0);
		opacity: 1;
	}

	85% {
		stroke-dasharray: 1000;
		stroke-dashoffset: 1000;
		transform: translate(1px, -5px);
		opacity: 1;
	}

	90% {
		stroke-dashoffset: 1000;
		stroke-dashoffset: 1000;
		transform: translate(2px, -8px);
		opacity: 0;
	}

	100% {
		stroke-dashoffset: 1000;
		stroke-dashoffset: 500;
		transform: translate(2px, -8px);
		opacity: 0;
	}
}

@keyframes nelio-ab-testing-confetti-cone-frames__confa {

	0% {
		opacity: 0;
		transform: translate(-30px, 20px) rotate(0);
	}

	15% {
		opacity: 1;
		transform: translate(25px, -10px) rotate(60deg);
	}

	80% {
		opacity: 1;
		transform: translate(33px, -18px) rotate(180deg);
	}

	100% {
		opacity: 0;
		transform: translate(37px, -23px) scale(0.5) rotate(230deg);
	}
}

@keyframes nelio-ab-testing-confetti-cone-frames__confb {

	0% {
		opacity: 0;
		transform: translate(-30px, 20px) rotate(0);
	}

	12% {
		opacity: 1;
		transform: translate(25px, -10px) rotate(60deg);
	}

	76% {
		opacity: 1;
		transform: translate(33px, -18px) rotate(180deg);
	}

	100% {
		opacity: 0;
		transform: translate(37px, -23px) scale(0.5) rotate(240deg);
	}
}

@keyframes nelio-ab-testing-confetti-cone-frames__confc {

	0% {
		opacity: 0.7;
		transform: translate(-30px, 20px) rotate(0);
	}

	18% {
		opacity: 1;
		transform: translate(5px, -10px) rotate(60deg);
	}

	76% {
		opacity: 1;
		transform: translate(13px, -18px) rotate(180deg);
	}

	100% {
		opacity: 0;
		transform: translate(17px, -23px) scale(0.5) rotate(230deg);
	}
}

@keyframes nelio-ab-testing-confetti-cone-frames__confd {

	0% {
		opacity: 0.7;
		transform: translate(-20px, 20px) rotate(0);
	}

	18% {
		opacity: 1;
		transform: translate(-5px, -10px) rotate(60deg);
	}

	76% {
		opacity: 1;
		transform: translate(-8px, -18px) rotate(180deg);
	}

	100% {
		opacity: 0;
		transform: translate(-10px, -23px) scale(0.5) rotate(230deg);
	}
}
