.page-title-action.nab-ai-generate-test-title-action {
	background: linear-gradient(
			45deg,
			rgb(6, 147, 227) 0%,
			rgb(155, 81, 224) 100%
		)
		border-box;
	border-color: transparent;
	color: #fff;
	height: auto;
	transition: background-position 200ms ease-in-out;

	&:hover,
	&:active {
		background: linear-gradient(
				45deg,
				rgb(6, 147, 227) 0%,
				rgb(155, 81, 224) 100%
			)
			border-box;
		border-color: transparent;
		color: #fff;
	}
}
