@use "~admin-stylesheets/colors";

.nab-ai-test-generation-modal {
	max-height: none;
	height: calc(100vh - 4em);
	width: calc(100vw - 4em);

	.components-modal__header {
		background: linear-gradient(
				45deg,
				rgb(6, 147, 227) 0%,
				rgb(155, 81, 224) 100%
			)
			border-box;
		padding: 24px 32px;

		h1 {
			color: #fff;
		}

		button.components-button.has-icon {
			color: #fff;

			&:focus {
				box-shadow: 0 0 0 var(--wp-admin-border-width-focus) #fff9;
				outline-color: transparent;
			}
		}

		+ div {
			max-width: 60em;
			margin: 0 auto;

			&:has(.nab-ai-test-generation-modal__intro),
			&:has(.nab-ai-test-generation-modal__done),
			&:has(.nab-ai-test-generation-modal__loading) {
				display: flex;
				flex-direction: column;
				justify-content: center;
				min-height: calc(100% - 10em);
			}
		}
	}

	&__loading {
		margin-top: 3em;
	}

	&__intro-content {

		&-description {
			font-size: 1.2em;
		}

		&-settings {
			margin-top: 3em;
			padding: 2em;
			background: #f2f6fb;
			border-radius: 3px;

			h2 {
				margin-top: 0;
			}

			.nab-ai-privacy-settings {
				margin-top: 0;
			}

			.nab-google-analytics-data-setting {
				margin-top: 2em;

				&__property {
					margin-left: 2rem;
				}
			}
		}

		&-actions {
			margin-top: 3em;
			display: flex;
			justify-content: flex-end;
			gap: 0.5em;
		}
	}

	&__loading-message {
		text-align: center;
		margin: 0;
		margin-top: 1em;
		font-size: 1.5em;
	}

	&__candidate {
		border: 1px solid colors.$color__border-editor;
		border-radius: 3px;
		padding: 1.5em;
		margin-bottom: 1em;
		box-shadow: 5px 5px 5px colors.$color__box-shadow;

		&-info {
			display: flex;
			flex-direction: row;
			gap: 1.5em;
			margin-bottom: 1.5em;
		}

		&-preview {
			flex-shrink: 0;
			flex-grow: 0;
			width: 200px;

			figure {
				border: 1px solid colors.$color__border-editor;
				border-radius: 3px;
				overflow: hidden;
			}

			.nab-preview {
				width: 200px;
				margin: 0;
				height: 130px;
			}
		}

		&-priority {
			float: right;

			.dashicon.dashicons {
				color: var(
					--wp-components-color-accent,
					var(--wp-admin-theme-color, #3858e9)
				);
				font-size: 14px;
				height: 1em;
				width: 1em;
			}
		}

		&-content {
			flex-grow: 1;
		}

		&-type {
			font-weight: 700;
			background: #8298b3;
			color: #fff;
			display: inline-block;
			padding: 0.2em 1em;
			border-radius: 1em;
			font-size: 0.8em;
		}

		&-name {
			font-weight: 700;
			font-size: 1.5em;
			margin: 0;
		}

		&-rationale {
			margin-bottom: 1.2em;
		}

		&-hypotheses-actions {
			text-align: right;
		}

		&-hide-testing-ideas-button {
			margin: 2em auto 1em !important;
			display: block !important;
		}
	}

	&__hypothesis-selection-content h3:not(:first-child) {
		margin-top: 3em;
	}

	&__hypothesis {
		padding: 1.5em;
		margin-bottom: 1.5em;
		background: #f2f6fb;
		transition: color 200ms ease-in-out;

		&--is-used:not(:hover):not(:focus-within) {
			color: #8298b3;
		}

		&-icon {
			width: 2em;
		}

		&-name {
			font-weight: 700;
			font-size: 1.2em;
			margin: 0;
		}

		&-block {

			.dashicon {
				color: #8298b3;
			}

			strong {
				margin-left: 0.5em;
			}

			p {
				margin-top: 0;
			}
		}

		&-actions {
			align-items: center;
			display: flex;
			flex-direction: row;
			gap: 0.5em;
			justify-content: flex-end;
			transition: opacity 200ms ease-in-out;

			.components-base-control__field {
				margin-bottom: 0 !important;
			}
		}

		&--is-used:not(:hover):not(:focus-within) #{&}-actions {
			opacity: 0.6;
		}
	}

	&__done {
		display: flex;
		flex-direction: column;
		justify-content: center;

		&-title {
			font-size: 2em;
			font-weight: 700;
			margin: 1em 0;
			text-align: center;
		}

		&-icon {
			margin: 0 auto;
			width: 250px;
			padding-left: 90px;
			box-sizing: content-box;
		}

		&-description {
			max-width: 30em;
			margin: 4em auto 1em;
		}

		&-actions {
			display: flex;
			gap: 0.5em;
			justify-content: center;
			margin-top: 1em;
		}
	}
}
