@use "~admin-stylesheets/colors";

.nab-google-analytics-data-setting {

	&__property {
		margin-left: calc(
			var(--checkbox-input-size, 0) + var(--checkbox-input-spacing, 0)
		);
		margin-top: 1rem;

		label {
			display: block;
		}
	}

	label:not([for="nab-google-analytics-data-setting"]) {
		text-transform: none;
		font-size: inherit;
		font-weight: inherit;
	}

	.components-text-control__input {
		border-radius: 4px;
		border: 1px solid #8c8f94;
		max-width: 20em;
		resize: vertical;
	}

	&__connection {
		margin: 1.5em 0;

		&__overlay {
			background: colors.$color__component-overlay-background;
			bottom: 0;
			display: flex;
			height: 100vh;
			left: 0;
			position: fixed;
			right: 0;
			top: 0;
			width: 100vw;
			z-index: 9999999;
		}

		&__overlay-content {
			align-self: center;
			color: colors.$color__component-overlay-foreground;
			font-size: 1.4em;
			margin: 0 auto;
			padding: 2rem;
		}

		&__selector {
			max-width: 20em;
		}

		.components-base-control__help {
			font-size: 13px;
			margin-bottom: 1em;
		}
	}
}
