.nab-premium-feature {
	background: #fde68a;
	border-radius: 16px;
	color: #78350f;
	margin: -0.2em;
	padding: 0.2em;

	&:hover:not(:disabled),
	&:active:not(:disabled) {
		box-shadow: none;
		color: #78350f;
	}

	&:focus:not(:disabled) {
		box-shadow: 0 0 0 var(--wp-admin-border-width-focus) #78350f;
		color: #78350f;
		outline-color: transparent;
	}
}
