@use "~admin-stylesheets/colors";

.nab-quota-meter {

	&__title {
		font-size: 0.9em;
		margin-bottom: 0;
		margin-top: 5px;
	}

	&__amount {
		float: right;

		&--empty {
			font-weight: 600;
		}
	}

	&__warning-icon {
		color: colors.$color__background-red;
		font-size: 16px;
	}

	&__bar-container {
		background-color: colors.$color__background-grey;
		border-radius: 3px;
		height: 20px;
		padding: 0;
		position: relative;
	}

	&__bar {
		border-radius: 3px;
		display: block;
		height: 100%;
		overflow: hidden;
		position: absolute;
		width: 100%;
		background-color: colors.$color__background-green;
		transition: background-color 500ms ease;

		@for $i from 0 through 100 {
			&--width-#{$i} {
				animation: nab-shrink-quota-#{$i} 500ms 2s ease forwards;
				transition: background-color 500ms ease;
			}

			@keyframes nab-shrink-quota-#{$i} {

				from {
					width: 100%;
					background-color: colors.$color__background-green;
				}

				to {
					width: $i * 1%;

					@if ($i <= 15) {
						background-color: colors.$color__background-red;
					} @else if ($i <= 30) {
						background-color: colors.$color__background-orange;
					} @else {
						background-color: colors.$color__background-green;
					}
				}
			}
		}

		&--disabled {
			background-color: colors.$color__text-disabled !important;
		}
	}
}
